import { ref, watch } from 'vue'
import useCollection from '@/firebase/useCollection'
import { io } from "socket.io-client";
import feedbackHandler from '@/features/feedback/feedbackHandler';




const { set } = useCollection()

const roomId = ref()
export const code = ref('')
export const theme = ref('solarized_dark')
export const outputs = ref([]);
export const inSession = ref([])
export const coderIsTyping = ref()
export const socketId = ref()
export const chatMessages = ref([])
export const mousePositions = ref([])
export const mouseTraceEnabled = ref(false);

let socket = null

export const sendChatMessage = (message) => {
    socket.emit('chat-message', { message, roomId: roomId.value, socketId: socket.id })
}

export const emitCursorPosition = (x, y) => {
    socket.emit('cursor-position', { message: { x, y }, roomId: roomId.value, socketId: socket.id })
}

export const stopEmitCursorPosition = () => {
    socket.emit('stop-cursor-position', { roomId: roomId.value, socketId: socket.id })
}

export const establishSocketConnection = (id: any) => {
    roomId.value = id
    // socket = io("http://localhost:5000", {
    socket = io("https://brocoder-api.herokuapp.com/", {
        reconnectionDelayMax: 10000,
        query: {
            roomId: id
        }
    })

    socket.on('connect', () => {
        socketId.value = socket.id
    })

    let watcher = watch(code, (val) => {
        socket.emit('code-payload', { code: val, id: roomId.value, coder: socket.id })
    })
    socket.on('code-payloadr', (payload) => {
        watcher()
        code.value = payload.code
        watcher = watch(code, (val) => {
            socket.emit('code-payload', { code: val, id: roomId.value, coder: socket.id })
        })
    })

    socket.on('output-payload', (payload) => {
        outputs.value.push(payload)
    })

    socket.on('in-session', (payload) => {
        if (payload.message.length < inSession.value.length) {
            mousePositions.value = mousePositions.value.filter(user => payload.message.includes(user.socketId))
            feedbackHandler.setFeedback({ message: 'A bro left the session.', type: 'message' })

        } else if (inSession.value.length && payload.message.length > 1) {
            feedbackHandler.setFeedback({ message: 'A bro joined the session.', type: 'message' })
        }
        inSession.value = payload.message
    })
    socket.on('coder-typing', (payload) => {
        coderIsTyping.value = payload.message
    })

    socket.on('session-chat-message', payload => {
        console.log(payload)
        chatMessages.value.unshift(payload)

    })

    socket.on('user-cursor-position', payload => {
        const userIndex = mousePositions.value.findIndex(user => user.socketId === payload.message.socketId)
        if (userIndex !== -1) {
            mousePositions.value[userIndex].x = payload.message.x
            mousePositions.value[userIndex].y = payload.message.y

        } else {
            mousePositions.value.push(payload.message)
        }
    })

    socket.on('user-stop-cursor-position', (payload) => {
        mousePositions.value = mousePositions.value.filter(user => user.socketId !== payload.socketId)
    })
}

export const clearLog = () => {
    outputs.value = []
}

export const saveCode = async (id: string) => {
    await set({ code: code.value, id }, id)
}